@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600;700&display=swap);
:root {
    --light-blue: #007EA1;
    --dark-blue: #003E66;
    --orange: #E67F20;
    --dark-orange: #FD6317;
    --green: #00C889;
    --gray: #808080;
    --black-opacity: rgba(0,0,0,.1);
    --light-gray: #F4F9FE;
    --english: #0098FC;
    --language: #FF7301;
    --naturals: #00C889;
    --math: #FF474A;
    --socials: #006EC9;
    --critic: #FFBA01;
    --sexual: #93278F;

}

h1, h2, h3, h4, h5, p, a, span, em, b, label, input, li, ul{
    font-family: 'Open Sans', sans-serif;
    padding: 0;
    margin: 0;
}

h2{
    display: block;
    font-size: 30px;
    width: 100%;
    text-align: center;
    padding: 40px;
    font-weight: bold;
    color: #007EA1;
    color: var(--light-blue);
}

h2:after{
    content: ' ';
    width: 50px;
    background-color: #E67F20;
    background-color: var(--orange);
    height: 3px;
    margin-top: 10px !important;
    display: block;
    margin: 0 auto;
}

h2 small{
    font-size: 16px;
    color: #808080;
    color: var(--gray);
    display: block;
}

.back-gray{
    background-color: #F4F9FE;
    background-color: var(--light-gray);
}

a:hover{
    text-decoration: none;
}

.support-whatsapp{
    margin: 15px 0 0;
    font-size: 18px;
    position: fixed;
    right: 12px;
    bottom: 12px;
    z-index: 999;
    box-shadow: 0px 0px 10px rgba(0,0,0,.4);
}

/****bootstrap***/
.dropdown-menu li, .dropdown-menu li a{
    display: block !important;
    width: 100%;
    margin: 0 !important;
    border-radius: 0 !important;
}
.dropdown-menu li a{
    width: 100%;
}
.menu{
    padding: 20px 10px;
}

.menu ul li{
    display: inline-block;
    margin: 0 5px;
}

.menu ul li a{
    padding: 5px 15px;
    color: var(--light-blue);
    border-radius: 10px;
}

.menu ul li a:hover{
    text-decoration: none;
    background-color: var(--light-gray);
}

.top-bar{
    background-color: var(--light-blue);
    padding: 5px 0;
}

.top-bar ul li{
    display: inline-block;
    color: #fff;
}

.top-bar ul li a, .top-bar p{
    color: #fff;
}

.top-bar .social-media p, .top-bar .social-media ul{
    display: inline-block;
}


.top-bar .container, .container.menu{
    padding: 10px !important;
}


.advantage{
    background-color: var(--light-gray);
    padding: 40px !important;
    border-radius: 20px;
    margin-left: -80px;
}

.advantage h2{
    padding-bottom: 20px;
    padding-top: 0;
}

.advantage ul li::marker{
    color: var(--green);
}

.advantage ul li{
    color: #000;
}

.benefits-container{
    padding: 40px 0;
}

.benefits-container .card.benefits{
    padding: 20px;
    border-radius: 20px;
    margin: 10px;
    -webkit-flex: 31% 1;
            flex: 31% 1;
    border: 0;
    box-shadow: 0px 0px 3px var(--black-opacity);
    
}

.benefits-container .card.benefits .text-benefit{
    width: calc(100% - 80px);
    display: inline-block;
    vertical-align: middle;
    padding: 10px;
}

.benefits-container .card.benefits img{
    max-width: 80px;
    display: inline-block;
    vertical-align: middle;
}

.benefits-container .card.benefits .text-benefit h4{
    font-size: 16px;
    color: var(--light-blue);
    font-weight: bold;
}

.benefits-container .card.benefits .text-benefit p{
    font-size: 14px;
}

.pt-80{
    padding: 80px 20px;
}

