.main-banner{
    background-image: url(/static/media/banner-ukanbook-2021-10.e42858c7.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 80px 20px 150px 20px;
    background-position: center;
}

.main-banner .text-banner{
    color: #fff;
    font-size: 30px;
}

.main-banner .login-form{
    padding: 40px 20px;
    background-color: rgba(255, 255, 255, .8);
    border-radius: 20px;
}

.main-banner .login-form h3{
    text-align: center;
    color: var(--light-blue);
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 15px;
}

.main-banner .login-form h3 small{
    display: block;
    color: var(--gray);
    font-size: 16px;
}

.main-banner .login-form input{
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    margin: 5px 0;
    border: 1px solid var(--gray);
}

.main-banner .login-form p{
    font-size: 14px;
    text-align: center;
    margin: 5px 0;
}

.main-banner .login-form p a{
    color: var(--orange);
    display: block;
    font-weight: bold;
}

.main-banner .login-form > a{
    font-size: 14px;
    color: var(--light-blue);
    font-weight: bold;
    text-align: center;
    margin: 5px 0;

}

.main-banner .login-form button{
    max-width: 300px;
    margin: 5px auto;
    display: block;
}

.article-container .meta-blog ul{
    list-style: none;
}

.article-container .meta-blog ul li{
    display: inline-block;
    padding: 5px;

}

.article-container .imgFeatured{
    border-radius: 20px;
}

.article-container h3.title-article{
    font-size: 20px;
    color: var(--light-blue);
    font-weight: bold;
    margin: 20px 0;
}

.article-container a{
    color: var(--orange);
    float: right;
    font-weight: bold;
    margin-top: 20px;
    padding: 5px 10px;
    border-radius: 10px;
}

.article-container a:hover{
    color: var(--orange);
    background-color: var(--light-gray);
}

.subjects-container{
    text-align: center;
    display: block;
}

.subjects-container .subject{
    margin: 10px;
    text-align: center;
    background-color: var(--light-gray);
    width: 12%;
    display: inline-block;
    padding: 20px;
    border: 0;
}

.subjects-container .subject img{
    width: 60px;
}

.subject p{
    text-transform: uppercase;
    font-size: 14px;
    color: #fff;
    margin-top: 10px;
}

.english{
